import Vue from 'vue'
import Router from 'vue-router'
import frontendDashboard from '@/components/frontEnd/frontendDashboard'
import home from '@/components/frontEnd/home'
import about from '@/components/frontEnd/about'
import signin from '@/components/frontEnd/signin'
import frontEndproducts from '@/components/frontEnd/products'
import checkOut from '@/components/frontEnd/checkOut'
import checkOrder from '@/components/frontEnd/checkOrder'

import dashboard from '@/components/backEnd/dashboard'
import products from '@/components/backEnd/products'
import orders from '@/components/backEnd/orders'
import coupons from '@/components/backEnd/coupons'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/' , redirect: 'home',
      name: 'frontendDashboard',
      component: frontendDashboard,
      children : [
        {
          path : 'home',
          name : 'home',
          component : home
        },
        {
          path : 'about',
          name : 'about',
          component : about
        },
        {
          path : 'signin',
          name : 'signin',
          component : signin
        },
        {
          path : 'products',
          name : 'frontEndproducts',
          component : frontEndproducts
        },
        {
          path : 'checkOut',
          name : 'checkOut',
          component : checkOut
        },
        {
          path : 'checkOrder/:orderId',
          name : 'checkOrder',
          component : checkOrder
        }
      ]
    },
    {
      path: '/admin',
      name: 'dashboard',
      component: dashboard,
      children : [
        {
          path: 'products',
          name: 'products',
          component: products,
          meta : {requiresAuth : true}
        },
        {
          path: 'orders',
          name: 'orders',
          component: orders,
          meta : {requiresAuth : true}
        },
        {
          path: 'coupons',
          name: 'coupons',
          component: coupons,
          meta : {requiresAuth : true}
        }
      ]
    }
  ]
})



