        <!-- 產品區banner -->
        <section>
            <div class="container">

                <div class="product-banner-border">
                <div class="products-banner">

                    <vue-typer
                    :text='["不斷的突破光學極限","只為了登峰造極"]'
                    :repeat='Infinity'
                    :shuffle='false'
                    initial-action='typing'
